
.hint {
  color: #41b883;
}
.hint:hover {
  cursor: pointer;
}
.finished {
  text-decoration: line-through;
}
.progress {
  color: #41b883;
  font-weight: bold;
}
.list {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  width: 50%;
}
.list-input {
  border-bottom: 1px solid #41b883;
  margin-bottom: 1em;
}
.list-input > input {
  width: 100%;
  border: none;
  padding: 0.5em;
  font-size: medium;
}
.date-input {
  width: 100%;
  border: none;
  font-size: medium;
  font-family: inherit;
  color: inherit;
}
.list-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  padding: 0.5em;
  border-bottom: solid 1px #eee;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.list-item > span {
  text-align: left;
  margin-left: 1em;
}
.item-text {
  width: 80%;
}
.item-due {
  width: 50%;
  text-align: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 50%;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.hint-edit {
  margin-left: 5px;
}
.archive {
  visibility: hidden;
}
.btn {
  border-radius: 5px;
  font-size: medium;
}
.btn:hover {
  cursor: pointer;
}
.reset-btn {
  padding: 1em;
  height: auto;
  color: white;
  background: #2C3E50;
  border: 2px solid #2C3E50;
}
.reset-btn:hover {
  background: white;
  color: #2C3E50;
}
.delete-btn {
  margin-left: 20px;
  width: 50px;
  height: auto;
  border: 2px solid #E91E63;
  background: white;
  color: #E91E63;
  font-weight: 800;
}
.delete-btn:hover {
  color: white;
  background: #E91E63;
}
footer {
  position: absolute;
  bottom: 20px;
  left: 45%;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}



